.default-background {
  background-color: #E7E7E7;
}
.exploreheader {
  width: 100%;
  height: 80px;
  text-align: left;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #344146;
  margin: 0px;
}
.exploreheader h1 {
  font-family: 'Libre Baskerville', serif;
  font-size: 15px;
  float: left;
  margin: 0px 15px;
  color: white;
  line-height: 80px;
}
.searchtext {
  width: 20%;
  height: 30%;
  border-radius: 5px;
  float: right;
  margin: 15px 15px 15px 0px;
  padding: 10px;
  background-color: white;
  border: 0;
}
.searchtext:focus {
  border: 0;
  border-radius: 5px;
  height: 30%;
  box-shadow: 0 0 5px 1px white;
  width: 35%;
  animation: textboxanimation 0.5s;
  margin: 15px 15px 15px 0px;
  float: right;
  padding: 10px;
}
@keyframes textboxanimation {
  from {
    border: 0;
    width: 20%;
  }
  to {
    box-shadow: 0 0 5px 1px white;
    width: 35%;
  }
}
.searchresult {
  top: 15%;
  width: 80%;
  display: block;
  position: absolute;
  margin: 20px;
}
.entry {
  display: block;
  background-color: white;
  padding: 20px;
  box-shadow: 3px 3px 5px 6px #ccc;
}
.entry h2 {
  margin: 0px;
  padding: 0px;
}
.entry p {
  margin: 10px 0px 10px 0px;
  padding: 0px;
}
.home {
  display: block;
  height: 80px;
  width: 80px;
  float: left;
  background: transparent url('../img/house.svg') center center no-repeat;
  background-size: 40%;
  z-index: 2;
}
